import React from 'react';
import { Table, Row, Col, Tabs, Tab, Nav } from 'react-bootstrap';
import Card3 from '../../components/Card/Card3';
import Activo from './partes/Activo';
import {activos} from '../../x';
import Help from '../../components/Help/Help';
import {lang, doc} from '../../lang/lang';

const Activos = (promp) => {
    require('../../assets/themes/theme1/estilo.scss')
    const {tp, id} = promp
    let bag = []
    for(let i=1; i < activos.length ;i++){
        let img = ''
        if( activos[i].img != undefined && activos[i].img.lenght > 0 )
            img = activos[i].img[0].src
            bag.push(<Activo 
                id={activos[i].id}
                idx={activos[i].idx}
                img={img}
                tp={activos[i].tp}
                de={activos[i].de} 
                cli={activos[i].cliente.de}
                cli_doc={activos[i].cliente.documento}
                cli_telf={activos[i].cliente.telf}
                cli_email={activos[i].cliente.email}
                repre={activos[i].cliente.representante}
                repre_doc={activos[i].cliente.documento_re}
                loc={activos[i].locatario.de}
                loc_doc={activos[i].locatario.documento}
                loc_telf={activos[i].locatario.telf}
                loc_email={activos[i].locatario.email}
            />)
        
    }

    return (
        <React.Fragment>
            <Help contenido={'<b>' +doc.personas +':</b> ' +doc.personas +"<br /> " +doc.personas}/>
            
            <Card3>
                <Table responsive hover>
                    <thead>
                        <tr className="unread" style={{background: '#000', color: '#ff0', fontWeight: '700', textTransform: 'uppercase'}}>
                            <td style={{witdh: '1.5em'}}></td>
                            <td>{lang.cliente}</td>
                            <td>{lang.activo}</td>
                            <td>{lang.locatario}</td>
                        </tr>
                    </thead>
                    <tbody>
                        {bag}
                    </tbody>
                </Table>
           </Card3>
        </React.Fragment>
    );
};
export default Activos;